.App {
  text-align: center;
}

.question{
  font-size: 28px;
  font-family:arial;
  font-weight: bold;
  margin-bottom: 15px;
}
.options {
  font-size: 22px;
  border: none;
  border: 2px solid black;
  cursor: pointer;

}
li {
  list-style: none;
  padding: 25px;
  margin: 30px;
  background-color: #bdc3c7;
  box-shadow: 10px 10px 5px black;
  border-radius: 15px;
}
.incorrect {
  background-color: red;
}
.finish {
  text-align: center;
  color: white;
  background: radial-gradient(#dfe6e9, #b33939);
  font-size: 42px;
  margin-top: 250px;
  padding: 50px;
  box-shadow: 10px 10px 5px black;
  border: 2px solid red;
}
input[type="button"] {
  margin: 25px;
}

